"use client";

import { MapContainer, Marker, TileLayer, Tooltip, Popup } from "react-leaflet";
import { LatLngTuple, LatLngExpression } from "leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-defaulticon-compatibility";
import L from "leaflet";
import "leaflet-defaulticon-compatibility/dist/leaflet-defaulticon-compatibility.webpack.css";

interface MapProps {
  position: LatLngExpression | LatLngTuple;
  zoom: number;
}

// Set the default position and zoom level
const defaults = {
  position: [40.7685, -73.9822] as LatLngTuple,
  zoom: 14,
};

const CarIcon = L.icon({
  iconUrl: require("../../public/police_car.png"),
  iconRetinaUrl: require("../../public/police_car.png"),
  iconAnchor: undefined,
  popupAnchor: [-0, -0],
  iconSize: [32, 45],
  className: "leaflet-div-icon",
});

export default function Map(MapProps: MapProps) {
  const { position = defaults.position, zoom = defaults.zoom } = MapProps;

  return (
    <MapContainer
      center={position}
      zoom={zoom}
      scrollWheelZoom={false}
      style={{ height: "100%", width: "100%" }}
    >
      <TileLayer
        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      <Marker position={position} icon={CarIcon}>
        {/* <Popup />
        <Tooltip>
          <span className="italic text-lg">Central Park</span>
          <p>This is a park designed by Frederick Law Olmsted</p>
        </Tooltip> */}
      </Marker>
    </MapContainer>
  );
}
